<template>
<div  class="articleAdmin">
      <b-form>
        <input id="article-id" type="hidden" v-model="article.id" />
        <b-row>
          <b-col xs="12">
            <b-form-group label="Nome" label-for="article-name">
              <b-form-input id="article-name" type="text" v-model="article.name" required :readonly="mode =='remove'" placeholder="Informe o nome do Artigo" />
            </b-form-group>
          </b-col>
        </b-row>

        <b-row>
          <b-col xs="12">
            <b-form-group label="Descrição" label-for="article-description">
              <b-form-input id="article-description" type="text" v-model="article.description" required :readonly="mode =='remove'" placeholder="Informe a descrição" />
            </b-form-group>
          </b-col>
        </b-row>

        <b-row>
          <b-col xs="12" v-if="mode ==='save'">
            <b-form-group label="Imagem: (URL)" label-for="article-imageUrl">
              <b-form-input id="article-imageUrl" type="text" v-model="article.imageUrl" required :readonly="mode =='remove'" placeholder="Informe a URL da imagem..." />
            </b-form-group>
          </b-col>
        </b-row>

          <b-row v-show="mode ==='save' ">
            <b-col xs="12">
              <b-form-group label="Categoria:" label-for="article-categoryId">
                  <b-form-select id="article-categoryId" :options="categories" v-model="article.categoryId" />
              </b-form-group>
            </b-col>
          </b-row>

           <b-row v-show="mode ==='save' ">
            <b-col xs="12">
              <b-form-group label="Autor:" label-for="article-userId">
                  <b-form-select id="article-userId" :options="users  " v-model="article.userId" />
              </b-form-group>
            </b-col>
          </b-row>

        <b-form-group label="Counteúdo" label-for="article-content" v-if="mode ==='save'">
          <VueEditor v-model="article.content" placeholder="Informe o Conteúdo do artigo!!" />
        </b-form-group>

      <b-button variant="primary" v-if="mode === 'save'" @click="save">Salvar</b-button>
      <b-button variant="danger" v-if="mode === 'remove'" @click="remove">Excluir</b-button>
      <b-button class="ml-2" @click="reset">Cancelar</b-button>

      </b-form>

      <div v-for="article in articles" :key="article.id">
        {{article.id}}
        {{article.name}}
        {{article.description}}
         <b-button variant="warning"  class="mr-2">
            <i class="fa fa-pencil"></i>
          </b-button>
          <b-button variant="danger">
            <i class="fa fa-trash"></i>
        </b-button>

      </div>
      
      <b-pagination size="md" v-model="page" :total-rows="count" :per-page="limit" />
  </div>
</template>

<script>
import {VueEditor} from 'vue2-editor'
import {baseApiUrl,showError} from '@/global'
import Axios from 'axios'

export default {
name:'ArticleAdmin',
components:{VueEditor},
    data: function(){
      return{
        mode: 'save',
        article: {},
        articles: [],
        categories: [],
        users: [],
        page: 1,
        limit: 0,
        count: 0,
        fields: [
          {key: 'id', label: 'Código', sortable: true},
          {key: 'name', label: 'Nome', sortable: true},
          {key: 'description', label: 'Descrição', sortable: true},
          {key: 'actions', label: 'Ações'},

        ]
      }
    },
    methods:{
      loadArticles(){
        const url = `${baseApiUrl}/articles?page=${this.page}`
        Axios.get(url).then(res => {
          this.articles = res.data.data;
          this.count = res.data.count;
          this.limit = res.data.limit
          })
      },
      reset(){
        this.mode = 'save'
        this.article = {}
        this.loadArticles()
      },
      save(){
        const method = this.article.id ? 'put' : 'post'
        const id = this.article.id ? `/${this.article.id}` : ''
        Axios[method](`${baseApiUrl}/articles${id}`, this.article)
          .then(()=>{
            this.$toasted.global.defaultSuccess()
            this.reset()
          })
          .catch(showError)
      },
      remove(){
        const id = this.article.id
        Axios.delete(`${baseApiUrl}/articles/${id}`)
          .then(()=>{
            this.$toasted.global.defaultSuccess()
            this.reset()           
          })
          .catch(showError)
      },
      loadArticle(article, mode= 'save'){
        this.mode = mode
        Axios.get(`${baseApiUrl}/articles/${article.id}`)
          .then(res => this.article = res.data)
      },
      loadCategories(){
        const url = `${baseApiUrl}/categories`
        Axios.get(url).then(res => {
          this.categories = res.data.map( category =>{
            return { value: category.id, text: category.path}
          })
        })
      },
      loadUser(){
        const url = `${baseApiUrl}/users`
        Axios.get(url).then(res => {
          this.users = res.data.map( user =>{
            return { value: user.id, text: `${user.name} - ${user.email}`}
          })
        })

      }
    },
    watch: {
        page() {
          this.loadArticles()
        }
    },
    mounted(){
      this.loadUser();
      this.loadCategories();
    this.loadArticles()
     
    }
}
</script>

